import { Component, OnInit, Output, EventEmitter, HostListener, Input } from '@angular/core';
import { Sections } from '../shared/sections';
import { ScrollOffset } from '../types/scroll-offset';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {
  @Input() sectionsOffset: ScrollOffset[];
  @Output() onNavClicked = new EventEmitter<{element: string, activeSection: number}>();
  @HostListener('window:scroll', ['$event'])
  onWindowScroll() {
    let element = document.querySelector('nav') as HTMLElement;
    if (window.pageYOffset > element.clientHeight) {
      element.classList.add('navbar-inverse');
    } else {
      element.classList.remove('navbar-inverse');
    }

    this.sectionsOffset.forEach((s,index) => {
      if (window.pageYOffset >= s.offset && window.pageYOffset <= s.offset + s.height)
      {
        this.activeSection = index;
      }
    })
  }

  sections = Sections;
  activeSection:number = -1;
  collapsed:boolean = false;

  constructor() { }

  ngOnInit(): void {
  }

  onClicked(e: string, i: number) {
    this.onNavClicked.emit({element: e, activeSection: i});
    this.activeSection = i;
  }
}
